<template>
    <publicMWrap :title="t('Home.tools.title')">
        <div class="learning-tool">
            <img class="opt-btn pre" src="@/assets/images/common/arrow.png" @click="preHandler" />
            <div class="learning-tool-list">
                <div class="inner" ref="cardInner">
                    <div class="card" :id="`Glossary${k + 1}`" v-for="(v, k) in LEARNING_LIST" ref="cards"
                        @click="linkTo(v.path)">
                        <img :src="v.url" alt="" class="pic">
                        <div class="tool-name">{{ t(v.txt) }}</div>
                    </div>
                </div>
            </div>
            <img class="opt-btn next" src="@/assets/images/common/arrow.png" @click="nextHandler" />
        </div>
        <!-- <nuxt-link-locale class="more" to="/academy">More</nuxt-link-locale> -->
    </publicMWrap>
</template>
<script setup>
import { ref, computed, onMounted } from "vue"
import { LEARNING_LIST } from "@/data/image.js"
import { useCheckMobile } from "@/store/isMobile"
import { nuxtTo } from "@/utils/tools";
const { t } = useI18n();
const cards = ref(null);
const cardInner = ref(null);
const isMobile = computed(() => {
    return useCheckMobile().getItem()
})
let currentIndex = !isMobile.value ? ref(0) : ref(1); // 当前显示的第一张图片的索引
const imgToShow = ref(3); // 一次展示的图片数量
const clamp = (idx) => {
    const totalImg = cards.value?.length;
    let res = !isMobile.value ? Math.max(0, Math.min(Math.ceil(totalImg / imgToShow.value) - 1, idx)) : Math.max(0, Math.min(totalImg - 1, idx))
    return res
}
const scroll = (direction) => {
    if (direction === 'pre') {
        currentIndex.value--;
    } else {
        currentIndex.value++;
    }
    currentIndex.value = clamp(currentIndex.value);
    if (!isMobile.value) {
        // PC端
        cardInner.value.style.transform = `translateX(-${currentIndex.value * 104}%)`;
    } else {
        let move = 0
        move = currentIndex.value === 0 ? `translateX(${24}%)` : currentIndex.value === 1 ? `translateX(-${currentIndex.value * 33}%)` : `translateX(-${currentIndex.value * 33 + 23 * (currentIndex.value - 1)}%)`
        cardInner.value.style.transform = move;
    }

}
const preHandler = () => {
    scroll('pre')
}
const nextHandler = () => {
    scroll('next')
}
const linkTo = (url) => {
    let query = url.includes("?") ? getQuery(url) : {}
    nuxtTo(url, query)
}
// 判斷是IOS還是安卓
const getAgent = () => {
    const userAgent = navigator.userAgent.toLowerCase()
    let Android = userAgent.indexOf("android") != -1;
    let IOS = /(iphone|ipad|ipod|ios)/i.test(userAgent);
    return IOS ? "ios" : "android"
}
const isIOS = computed(() => {
    return getAgent() === 'ios' ? true : false
})
</script>
<style scoped lang='scss' src="./index.scss"></style>